<template>
    <view>
        <view style="
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    ">
            <view class="status-bar" :style="{
                height: statusBarHeight + 'px'
            }">
            </view>

            <view class="nav-bar" :style="{
                height: navHeight + 'px'
            }">
                <text class="nav-bar-title">
                    聊天
                    <text v-if="unreadMessageCount > 0" :style="{
                        'fontSize': '28rpx'
                    }">({{ unreadMessageCount }})</text>
                </text>
            </view>
        </view>

        <view :style="{
            'height': navHeight - (-statusBarHeight) + 'px'
        }"></view>

        <!-- 搜索框 -->
        <view class="search-box" id="searchBox">
            <view class="search-box-input" id="searchBoxHeight">
                <image :src="$kt.file.byPath('image/icon/search.svg')" mode="aspectFill" :style="{
                    'width': '30rpx',
                    'height': '30rpx',
                    'marginRight': '10rpx'
                }"></image>
                <text :style="{
                    'fontSize': '28rpx',
                    'color': '#999999'
                }">搜索昵称、备注或聊天记录</text>
            </view>
        </view>

        <view :style="{
            height: 120 + 'rpx'
        }"></view>


        <!-- 模仿微信做个好友列表 -->
        <view class="friend-list">
            <roomItem v-for="(item, index) in bodyData.content" :key="index" :item="item"></roomItem>
        </view>

        

        <view style="height: 120rpx;"></view>

    </view>
</template>

<script>

import roomItem from './inMessage/roomItem.vue';

export default {
    components: {
        roomItem
    },
    data() {
        return {
            ovoUserSelf: { id: "" },
            statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
            navHeight: 48,
            // #ifdef MP-WEIXIN
            menuButton: wx.getMenuButtonBoundingClientRect(),
            // #endif
            screenTop: 0,
            tSelect: 'recommend',
            searchBoxHeight: 0,
            requestParam: {
                pageNumber: 1
            },
            bodyData: {
                content: []
            },
            unreadMessageCount: 0,
            // 获取聊天室的次数
            getRoomCount: 0,
        };
    },
    mounted() {
        this.unreadMessageCount = this.$ovo.getUnreadMessageCount();
        uni.$on("changeUnreadMessageCount", () => {
            this.unreadMessageCount = this.$ovo.getUnreadMessageCount();
        });
        this.navHeight = 44;
        // #ifdef MP-WEIXIN
        this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
        // #endif

        uni.createSelectorQuery().in(this).select('#searchBox').boundingClientRect((rect) => {
            this.searchBoxHeight = rect.height;
        }).exec();

        this.ovoUserSelf = this.$ovo.getSelf();
        console.log(" this.ovoUserSelf", this.ovoUserSelf)


        // 查看是否有缓存
        var chatRoom = uni.getStorageSync("chatRoom");
        if (chatRoom) {
            this.bodyData.content = chatRoom;
        }

        this.getSelfRoom();


        uni.$on("newChat", (data) => {
            for (var i = 0; i < this.bodyData.content.length; i++) {
                if (this.bodyData.content[i].roomId == data.roomId) {
                    return false;
                }
            }
            this.getSelfRoom();
        });
    },
    methods: {
        getSelfRoom() {

            this.$kt.request.request({
                api: this.$api.business.ovo.chatRoom.getSelfRoom,
                data: this.requestParam,
                stateSuccess: (res) => {
                    if (this.getRoomCount == 0) {
                        this.bodyData.content = [];
                    }

                    for (var i = 0; i < res.data.content.length; i++) {
                        if (!this.isRepeat(res.data.content[i])) {
                            this.bodyData.content.unshift(res.data.content[i]);
                        }
                    }

                    this.getRoomCount++;
                    // 将聊天室的数据存入缓存
                    uni.setStorageSync("chatRoom", this.bodyData.content);
                }
            })
        },
        /**
         * 判断是否存在重复的
         */
        isRepeat(item) {
            for (var i = 0; i < this.bodyData.content.length; i++) {
                if (this.bodyData.content[i].roomId == item.roomId) {
                    return true;
                }
            }
            return false;
        }
    },
}
</script>

<style lang="scss" scoped>
.status-bar {
    background-color: #fff;
}

.nav-bar {
    position: relative;
    background-color: #fff;

    .nav-bar-icon {
        position: absolute;
        width: 45rpx;
        height: 45rpx;
    }

    .nav-bar-icon:active {
        transform: scale(0.9);
    }

    .nav-bar-title {
        position: absolute;
        left: 20rpx;
        font-size: 42rpx;
        top: 50%;
        transform: translateY(-50%);
        // 文字间距
        // letter-spacing: 5rpx;
    }

    .o-money-box {
        position: absolute;
        left: 50rpx;
        font-size: 28rpx;
        top: 50%;
        transform: translateY(-50%);
        color: #333333;
        font-weight: bold;
        background-color: #f0f0f0;
        padding: 5rpx 10rpx;
        border-radius: 10rpx;
    }
}


.friend-list {
    position: relative;
}


.search-box {
    position: fixed;
    padding: 20rpx;
    box-sizing: border-box;
    background-color: #fff;
    width: 100%;
    z-index: 10;

    .search-box-input {
        display: flex;
        align-items: center;
        height: 90rpx;
        background-color: #f0f0f0;
        border-radius: 30rpx;
        padding: 0 20rpx;
    }
}
</style>